import React, { Fragment } from 'react'
import asyncComponent from '../asyncComponent'
import './Projects.css'

const TopBar = asyncComponent(() => import('../TopBar/TopBar'))
const skills = [
  {
    img: 'https://biobii.github.io/static/img/work-porsigal-1.0cebfdb.png',
    name: 'Porsigal',
    link: 'http://porsigal.com'
  },
  {
    img:
      'http://cdn2.tstatic.net/banjarmasin/foto/bank/images/ilustrasi-sholat-dhuha_20180201_202406.jpg',
    name: 'Waktu Sholat API dan Push Notification',
    link: 'https://github.com/alfari16/waktu-sholat-api-dan-push-notification'
  },
  {
    img:
      'https://docs.microsoft.com/en-us/windows/desktop/rpc/images/httprpc1.png',
    name: 'JSON-RPC Client',
    link: 'https://github.com/alfari16/jsonrpc-client-http'
  }
].map((el, idx) => (
  <div className="single-project mb-2" key={idx}>
    <img src={el.img} className="project-img" alt={el.name} />
    <div className="overlay" />
    <a href={el.link} className="project-name">
      {el.name}
    </a>
  </div>
))

export default () => {
  return (
    <Fragment>
      <TopBar bgColor="#111111" color="white" icon="fa-pencil">
        Projects
      </TopBar>
      <div className="item-container">
        <div className="container">{skills}</div>
      </div>
    </Fragment>
  )
}



